/**
 * zent-breadcrumb:
 * `.zent-breadcrumb` - 
 *
 *     @example
 *     <div class="zent-breadcrumb">
 *         <a href="#"></a>
 *         <span></span>
 *     </div>
 *
 */
.zent-breadcrumb {
  color: #333;
  font-size: 0;
  line-height: 0;
  padding: 15px 0; }

.zent-breadcrumb span, .zent-breadcrumb a {
  font-size: 14px;
  line-height: 1em;
  display: inline-block;
  vertical-align: middle;
  text-decoration: none; }

.zent-breadcrumb span::after, .zent-breadcrumb a::after {
  content: "/";
  display: inline-block;
  vertical-align: middle;
  color: #bbb;
  margin-left: 5px;
  margin-right: 5px; }

.zent-breadcrumb span:last-child::after, .zent-breadcrumb a:last-child::after {
  content: "";
  display: none; }

.zent-breadcrumb span {
  color: #333; }

.zent-breadcrumb a {
  color: #38f; }

.zent-breadcrumb.zent-breadcrumb-nav {
  padding: 0; }

.zent-breadcrumb.zent-breadcrumb-nav a {
  display: inline-block;
  height: 50px;
  line-height: 48px;
  padding: 0 20px;
  min-width: 0;
  border: none;
  background: transparent;
  font-size: 14px;
  color: #666;
  text-align: center;
  box-sizing: border-box; }

.zent-breadcrumb.zent-breadcrumb-nav a:after {
  display: none; }

.zent-breadcrumb.zent-breadcrumb-nav .zent-breadcrumb-nav-active:after {
  display: block;
  color: #333;
  border-bottom: 2px solid #38f;
  content: '';
  margin: 0; }

.zent-btn {
  display: inline-block;
  height: 30px;
  line-height: 30px;
  padding: 0 15px;
  border-radius: 2px;
  font-size: 12px;
  font-family: inherit;
  color: #333;
  background: #fff;
  border: 1px solid #bbb;
  text-align: center;
  vertical-align: middle;
  box-sizing: border-box;
  cursor: pointer;
  -webkit-transition: all 0.3s;
  transition: all 0.3s; }

.zent-btn:focus {
  outline: none; }

.zent-btn:link, .zent-btn:visited, .zent-btn:focus {
  color: #333;
  text-decoration: none; }

.zent-btn:hover {
  color: #59f;
  border-color: #59f;
  text-decoration: none; }

.zent-btn:active {
  color: #333;
  background: #e5e5e5;
  border-color: #bbb;
  text-decoration: none; }

.zent-btn.zent-btn-loading:hover, .zent-btn.zent-btn-loading:active {
  background: #fff;
  border: 1px solid #bbb; }

.zent-btn[type="button"], .zent-btn[type="reset"], .zent-btn[type="submit"] {
  -webkit-appearance: none; }

.zent-btn + .zent-btn {
  margin-left: 10px; }

/*  */
.zent-btn-large {
  height: 38px;
  line-height: 36px;
  padding: 0 23px;
  font-size: 14px; }

.zent-btn-small {
  height: 26px;
  line-height: 24px;
  padding: 0 7px; }

.zent-btn-block {
  display: block;
  width: 100%;
  padding: 0; }

/*  */
.zent-btn-primary {
  color: #fff;
  background: #38f;
  border-color: #38f; }

.zent-btn-primary:link, .zent-btn-primary:visited, .zent-btn-primary:focus {
